.gallery__link { display: block; width: 387px; height: 340px; margin-bottom: 4px; }
@media only screen and (max-width: 768px) { .gallery__link { width: 100%; height: auto; } }
.gallery__link:hover .gallery__play { opacity: 0; }
.gallery__link:hover .gallery__plus { opacity: 1; }
.gallery__pic { display: block; max-width: 100%; border-radius: 4px; }
@media only screen and (max-width: 568px) { .gallery__pic { width: 100%; } }
.gallery__full { position: absolute; top: 0; right: 0; width: 40px; height: 40px; border-radius: 0 4px 0 0; background: #2E4F82; text-align: center; font-size: 0; color: #fff; }
.gallery__full:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.gallery__full .icon { font-size: 22px; vertical-align: middle; }
.gallery__play { position: absolute; top: 50%; left: 50%; width: 110px; height: 110px; margin: -55px 0 0 -55px; padding-left: 10px; border-radius: 50%; background: rgba(0, 0, 0, 0.6); text-align: center; font-size: 0; color: #fff; transition: opacity 0.2s; }
.gallery__play:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.gallery__play .icon { font-size: 50px; vertical-align: middle; }
.gallery__plus { position: absolute; top: 0; left: 0; right: 0; bottom: 0; background: rgba(255, 255, 255, 0.6); border-radius: 4px; opacity: 0; transition: opacity 0.2s; text-align: center; font-size: 0; color: #fff; }
.gallery__plus:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.gallery__plus .icon { font-size: 70px; vertical-align: middle; }
.gallery__sort { background: #ebebeb; padding: 5px 0; }
.gallery__sort-center { font-size: 0; }
.gallery__sort-item { display: inline-block; height: 62px; width: 190px; margin-right: 5px; border-radius: 4px; background: #fff; transition: background 200ms; color: #353535; text-align: center; text-align: center; font-size: 0; text-decoration: none; vertical-align: top; }
.gallery__sort-item:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.gallery__sort-item:hover { background: #90cc00; }
.gallery__sort-item:focus, .gallery__sort-item.is-active { background-image: linear-gradient(72deg, #1f9fe8 0%, #90cc00 100%); font-family: "OpenSans-ExtraBold"; font-weight: normal; font-style: normal; color: #FFF; }
.gallery__sort-title { display: inline-block; font-size: 14px; vertical-align: middle; }
.gallery__tags { padding: 30px 0; border-bottom: 1px solid #ebebeb; }
.gallery__top-title { padding: 30px 0; }
.gallery_gray { padding-bottom: 120px; background: #F2F2F2; }

.back { display: inline-block; height: 62px; min-width: 190px; padding: 0 20px; border-radius: 4px; background: #fff; transition: background 200ms; color: #353535; text-align: center; font-size: 0; white-space: nowrap; text-decoration: none; vertical-align: top; }
.back:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.back:hover, .back:focus, .back.is-active { background-image: linear-gradient(72deg, #1f9fe8 0%, #90cc00 100%); color: #FFF; }
.back__center { display: inline-block; vertical-align: middle; }
.back__icon, .back__title { display: inline-block; vertical-align: middle; }
.back__icon { padding-right: 15px; }
.back__icon .icon-arr-left { font-size: 20px; }
.back__title { font-family: "OpenSans-ExtraBold"; font-weight: normal; font-style: normal; font-size: 14px; text-transform: uppercase; }

.albums { margin-bottom: 90px; }
.albums__head { padding: 25px 0; }
.albums__list { margin: 0 -2px 40px; font-size: 0; }
.albums__item { display: inline-block; position: relative; width: 292px; margin: 0 2px 4px; vertical-align: top; }
.albums__link { display: block; background: #EBEBEB; border-radius: 4px; text-decoration: none; transition: background 0.2s; }
.albums__link:hover { background: #D8D8D8; }
.albums__link:hover .albums__plus { opacity: 1; }
.albums__preview { position: relative; height: 200px; }
.albums__plus { position: absolute; top: 0; left: 0; right: 0; bottom: 0; border-radius: 4px 4px 0 0; background: rgba(216, 216, 216, 0.6); text-align: center; font-size: 0; color: #fff; opacity: 0; transition: opacity 0.2s; }
.albums__plus:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.albums__plus .icon { font-size: 70px; vertical-align: middle; }
.albums__pic { display: block; max-width: 100%; border-radius: 4px 4px 0 0; }
.albums__info { padding: 10px; min-height: 170px; font-family: "OpenSans-Semibold"; font-weight: normal; font-style: normal; line-height: 1.375; }
.albums__title { margin-bottom: 5px; font-size: 16px; color: #323232; }
.albums__meta { font-size: 0; }
.albums__meta-item { display: inline; margin-right: 10px; font-size: 14px; color: #929292; }
.albums__meta-item:last-child:after { display: none; }
.albums__meta-item:after { content: ""; display: inline-block; width: 4px; height: 4px; margin-left: 10px; background: #929292; border-radius: 50%; vertical-align: middle; }
.albums__share { position: absolute; top: 0; right: 0; width: 40px; height: 40px; border-radius: 0 4px 0 0; background: #2E4F82; text-align: center; font-size: 0; color: #fff; transition: all 0.2s; z-index: 2; }
.albums__share:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.albums__share .icon { font-size: 20px; vertical-align: middle; }

.sitemap__list { font-size: 0; }
.sitemap__item { display: inline-block; width: 33.33%; margin-bottom: 50px; padding-right: 5.33%; vertical-align: top; }
.sitemap__title { padding: 0 25px 20px; font-size: 26px; }
.sitemap__link { display: block; padding: 12px 25px; border-bottom: 1px solid #e3e3e3; color: #787878; text-decoration: none; *zoom: 1; }
.sitemap__link:after { content: " "; display: table; clear: both; }
.sitemap__link:first-child { border-top: 1px solid #e3e3e3; }
.sitemap__link-arrow { float: left; margin: 5px 15px 0 0; font-size: 0; }
.sitemap__link-arrow .icon { font-size: 9px; }
.sitemap__link-title { display: table; font-family: "OpenSans-Semibold"; font-weight: normal; font-style: normal; font-size: 17px; }

.links__h1 { margin-bottom: 40px; }
.links__list { margin-bottom: 50px; }
.links__item { display: block; margin-bottom: 15px; border: 2px solid #e3e3e3; border-radius: 24px; color: #787878; text-decoration: none; *zoom: 1; }
.links__item:after { content: " "; display: table; clear: both; }
.links__item-arrow { float: right; width: 60px; height: 44px; text-align: center; font-size: 0; }
.links__item-arrow:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.links__item-arrow .icon { vertical-align: middle; }
.links__item-arrow .icon-arrow-link { font-size: 9px; }
.links__item-arrow .icon-arrow-download { font-size: 20px; }
.links__item-title { display: table; padding: 13px 22px; font-family: "OpenSans-Semibold"; font-weight: normal; font-style: normal; font-size: 14px; }


