// Normally mainpage styles

.header
	position: relative
	padding: 0 10px
	border-bottom: 4px solid #eaeaea
	.header__in
		position: relative
		max-width: 1400px
		+clr
		margin: 0 auto
		padding-left: 220px
		height: 95px
		background: #fff
		+r(1440)
			padding-left: 150px
		+r(1280)
			padding-left: 125px
		+r(1023)
			height: 75px	 	 
	.header__content
		float: right
		+clr
		+r(1023)
			display: none
			position: absolute
			top: 79px
			left: -10px
			right: -10px
			padding: 40px 20px
			background: #fff
			text-align: center
			z-index: 3  	 
.logo
	position: absolute
	top: -10px
	left: 0
	width: 220px
	height: 220px
	border-radius: 0 0 50% 50%
	border-width: 0 0 4px 4px
	border: 4px solid #eaeaea
	background: #fff
	z-index: 4
	&:before
		content: ""
		position: absolute
		top: -4px
		left: -4px
		right: -4px
		height: 105px
		background: #fff
		z-index: 1 
	a
		position: relative
		display: block
		width: 212px
		height: 212px
		border-radius: 50%
		font-size: 0	
		z-index: 2
	+r(1440)
		left: -25px
		width: 150px
		height: 150px
		a
			width: 142px
			height: 142px
			img
				width: 100% 
	+r(1023)
		&:before
			height: 85px				
	&_sm
		top: 0
		width: auto
		height: auto
		border: none
		&:before
			display: none
		a
			width: auto
			height: 95px
			+vertical
			img
				vertical-align: middle
		+r(1440)
			height: 95px
			a
				width: auto
				height: 95px
				img
					width: 34px
		+r(1023)					
			height: 75px
			a
				height: 75px
		
.nav
	float: left
	padding-top: 28px
	+r(1023)
		float: none
.menu
	+clr 
	.menu__link
		position: relative
		float: left
		padding: 4px 40px
		border-left: 1px solid #c4c4c4
		&:first-child
			border-left: none
		&:hover
			& > a
				&:after
					opacity: 1 
			.dropdown
				opacity: 1
				visibility: visible 	
		+r(1440)
			padding: 4px 30px
		+r(1280)
			padding: 4px 15px
		+r(1023)
			float: none
			padding: 10px
			border-bottom: 1px solid #d1d1d1
			border-left: none
			&:hover
				& > a
					&:after
						opacity: 0    	 	
		& > a
			position: relative
			display: inline-block
			font-size: 15px
			+opensans-eb
			text-decoration: none
			text-transform: uppercase
			line-height: 1
			z-index: 10
			&:after
				content: ""
				position: absolute
				bottom: -12px
				left: 0
				right: 0
				height: 12px
				border-bottom: 3px solid 
				opacity: 0
				transition: opacity 200ms
			.plus
				display: none
				position: absolute
				top: 50%
				right: 10px
				margin-top: -10px
				&:before
					content: ""
					position: absolute
					top: 0
					bottom: 0
					left: 8px
					width: 3px
					height: 19px
					background: #8c8c8c
					transition: transform 400ms
				&:after
					content: ""
					position: absolute
					top: 8px
					height: 3px
					width: 19px
					background: #8c8c8c
					transition: transform 400ms 	  
				+r(1023)
					display: block	
			&.is-open
				.plus
					&:before
						transform: rotate(90deg)
					&:after
						transform: rotate(180deg)			
			&.menu__about
				color: #8c8c8c
				&:after
					border-color: #8c8c8c
				.plus
					&:before, &:after
						background: #8c8c8c	 
			&.menu__programs
				color: $darkblue
				&:after
					border-color: $darkblue
				.plus
					&:before, &:after
						background: $darkblue	 
			&.menu__heroes
				color: #01afee
				&:after
					border-color: #01afee
				.plus
					&:before, &:after
						background: #01afee	
			&.menu__partners
				color: $gold
				&:after
					border-color: $gold
				.plus
					&:before, &:after
						background: $gold	
			&.menu__media
				color: #8bca0b
				&:after
					border-color: #8bca0b
				.plus
					&:before, &:after
						background: #8bca0b		
			+r(1023)
				font-size: 20px	
				width: 280px
				max-width: 100%
				margin: 0 auto 	
.dropdown
	position: absolute
	top: 100%
	left: 0
	width: 336px
	background: #fff
	padding: 25px
	opacity: 0
	visibility: hidden
	transition: opacity 200ms
	z-index: 9
	ul
		li
			border-top: 1px solid #c4c4c4
			&:first-child
				border-top: none 
			a
				display: block
				height: 38px
				line-height: 38px
				padding: 0 6px 
				font-size: 16px
				text-decoration: none
				color: #727272
				+opensans-sb
				transition: background 200ms
				&:hover
					background: #f1f1f1
	+r(1023)
		display: none
		position: relative
		top: auto
		left: auto
		width: 100%
		opacity: 1
		visibility: visible  				
					 
.support-link
	display: block
	margin-top: 28px
	float: left
	width: 146px
	height: 36px
	line-height: 36px
	border-radius: 3px
	text-align: center
	background: #eaeaea
	text-decoration: none
	span
		color: $orange
		text-decoration: none
		text-transform: uppercase
		font-size: 15px
		+opensans-eb
		transition: color 200ms, background 200ms
	&:hover
		background: $orange
		span
			color: #fff 
	+r(1023)
		display: block
		float: none
		width: 100%
		height: auto
		margin-top: 0
		padding: 5px 0 
		background: none
		font-size: 20px
		border-bottom: 1px solid #d1d1d1
		span
			font-size: 20px
		&:hover
			background: none
			span
				color: $orange  	
.humb
	display: none
	position: relative
	float: right
	width: 30px 
	margin-top: 24px
	margin-left: 30px
	margin-right: 20px
	padding: 11px 0
	background: none  
	span
		display: block
		height: 4px
		background: #9d9d9d
		transition: opacity 200ms
	&:before, &:after
		content: ""
		position: absolute
		left: 0
		right: 0
		height: 4px
		background: #9d9d9d
		transition: transform 300ms
		will-change: transform
	&:before
		top: 0
		transform-origin: 3px 1px
	&:after
		bottom: 0
		transform-origin: -3px 0
	&.is-open
		span
			opacity: 0
		&:before
			transform: rotate(45deg)	
		&:after
			transform: rotate(-45deg)	 	
	+r(1023)
		display: block			  
.lang
	position: relative
	float: right
	z-index: 4
	.lang__selected
		display: none
		cursor: pointer
		span
			display: inline-block
			vertical-align: middle
			margin-right: 10px 
			font-size: 16px
			text-transform: uppercase
			color: #9d9d9d
			+opensans-sb
		img
			display: inline-block
			vertical-align: middle
			width: 30px
			height: 30px
			border-radius: 50%
			border: 1px solid #dbdbdb 	 
	ul
		display: block
		font-size: 0
		margin-left: 34px
		margin-top: 28px 
		li
			display: inline-block
			vertical-align: middle
			margin-left: 15px
			&:first-child
				margin-left: 0
			button
				position: relative
				display: block
				background: none
				opacity: 0.5
				cursor: pointer
				transition: opacity 200ms
				will-change: opacity
				span
					display: none
					vertical-align: middle
					margin-right: 10px 
					font-size: 16px
					text-transform: uppercase
					color: #9d9d9d
					+opensans-sb
				img
					display: inline-block
					vertical-align: middle
					width: 36px
					height: 36px
					border-radius: 50%
					border: 1px solid #dbdbdb
				&.active, &:hover
					opacity: 1
	+r(1280)
		ul
			margin-left: 10px
			li
				margin-left: 5px
				button
					img
						width: 30px
						height: 30px	
	+r(1023)
		margin-top: 23px
		.lang__selected
			display: block
		ul
			opacity: 0
			visibility: hidden 
			position: absolute
			top: -10px
			left: 50%
			transform: translateX(-50%)
			margin: 0 
			background: #fff
			border-radius: 6px
			border: 2px solid #d1d1d1
			transition: opacity 200ms
			&.is-open
				opacity: 1
				visibility: visible 
			li
				margin-left: 0
				button
					width: 81px
					padding: 7px 0
					opacity: 1 
					span
						display: inline-block
					&.active
						background: #d1d1d1
						span
							text-decoration: underline	

// quotes
.quotes
	position: relative
	padding: 32px 0
	height: 550px
	background: url(../img/quotes-bg.jpg)
	+cover
	+r(1024)
		height: auto
	+r(900)
		padding: 0
	.quotes__in
		position: relative
		max-width: 1400px
		margin: 0 auto
		padding: 78px 150px
		overflow: hidden
		+r(1200)
			padding: 78px 80px
		+r(768)
			padding: 78px 60px 	
	.quotes__prev, .quotes__next
		position: absolute
		top: 50%
		left: 0
		height: 102px
		width: 102px  
		border: 4px solid #d7d7d7
		border-radius: 50%
		overflow: hidden
		cursor: pointer
		transform: translateY(-50%)
		opacity: 0.8 
		transition: opacity 200ms
		img
			position: absolute
			top: 50%
			left: 50%
			min-width: 100%
			max-width: 200%
			min-height: 100%
			max-height: 200%
			transform: translate(-50%, -50%)
		&:hover
			opacity: 1
		+r(1200)
			left: -51px	
	.quotes__next
		left: auto
		right: 0		
		+r(1200)
			left: auto
			right: -51px
	.quotes__item
		+clr
		&.slick-current
			.quotes__right
				display: block
	.quotes__left
		position: relative
		float: left
		margin-right: 15px
		font-size: 16px
		text-align: center
		+r(1024)
			margin-right: 20px
		+r(568)
			margin-right: 0
			float: none
			margin-bottom: 15px	
	.quotes__topper
		position: relative
		+r(768)
			margin-bottom: 20px
	.quotes__img
		position: relative
		width: 266px
		height: 266px
		margin-bottom: 15px
		border-radius: 50%
		overflow: hidden
		border: 4px solid #d7d7d7
		img
			position: absolute
			top: 50%
			left: 50%
			min-width: 100%
			min-height: 100%
			max-width: 2000em
			transform: translate(-50%, -50%)
		+r(1024)
			width: 200px
			height: 200px
		+r(768)
			width: 136px
			height: 136px
		+r(568)
			margin: 0 auto 		
	.quote__medals
		position: absolute
		bottom: 0
		right: 0
		z-index: 2
		+r(768)
			right: -6px
			bottom: -11px
			.round
				width: 58px
				height: 58px
		+r(568)
			right: 50%
			margin-right: -75px 		
	.quote__name, .quote__status
		font-size: 19px
		color: #fff
		+r(768)
			font-size: 14px
	.quote__name
		+opensans-sb
	.quote__status
		+opensans-l
	.quote__text
		margin-bottom: 75px
		padding-top: 35px
		font-size: 47px
		color: #fff
		+opensans-sb
		text-shadow: -1.4px 1.4px 2px rgba(0, 0, 0, 0.35)
		line-height: 55px
		+r(1440)
			font-size: 36px
			margin-bottom: 50px
		+r(1024)
			font-size: 30px
			margin-bottom: 30px
			line-height: 1.4
			padding-top: 15px
		+r(768)
			font-size: 22px
			padding-top: 0	 	  	 				
	.quotes__right
		overflow: hidden
		+r(568)
			text-align: center
	.quote__bottom
		.btn
			text-transform: none
			+opensans-sb
			padding: 20px 24px
			font-size: 18px
			+r(1024)
				padding: 15px 24px
			+r(768)
				padding: 10px 24px  	
		
.carousel
	position: relative
	.carousel__item
		position: relative
		height: 480px
	.carousel__title
		position: absolute
		top: 50px
		left: 10%
		color: #fff
		+opensans-eb
		text-transform: uppercase
		font-size: 22px
		z-index: 2
		+r(1023)
			left: 10px
			right: 10px
		+r(568)
			text-align: center
	.carousel__event
		position: absolute
		bottom: 50px
		right: 10%
		font-size: 17px
		color: #fff
		z-index: 2
		+r(1023)
			left: 10px
			right: 10px
		+r(568)
			text-align: center  

.m-news
	padding: 30px 0 
	.center
		padding: 0
	+r(1280)
		padding: 30px 10px 	
.tabs
	.tabs__topper
		font-size: 0
		padding: 0 10px
		margin-bottom: 40px
	.title
		float: left
		padding-top: 15px
		width: 150px
		+r(1280)
			float: none
			width: 100%
			text-align: center
			margin-bottom: 15px 
	.tabs-row
		position: relative
		height: 67px
		overflow: hidden
		.tabs-row__prev, .tabs-row__next
			visibility: hidden
			position: absolute
			top: 0
			height: 62px
			width: 50px
			cursor: pointer
			&.is-visible
				visibility: visible
		.tabs-row__prev
			left: 0
			background-image: linear-gradient(to left, rgba(#fff, 0) 0%, rgba(#fff, 0.7) 50%, rgba(#fff, 1) 100%)
			&:after
				content: ""
				position: absolute
				top: 23px
				left: 0
				+arr(12, 15, #707070, l)
		.tabs-row__next
			right: 0
			background-image: linear-gradient(to right, rgba(#fff, 0) 0%, rgba(#fff, 0.7) 50%, rgba(#fff, 1) 100%)
			&:after
				content: ""
				position: absolute
				top: 23px
				right: 0
				+arr(12, 15, #707070, r)	
		.tabs-row__in
			overflow-x: auto
	.tabs__nav
		display: inline-block
		vertical-align: middle
		font-size: 0
		white-space: nowrap
		li
			display: inline-block
			vertical-align: middle
			margin-left: 5px
			margin-bottom: 5px
	.tabs__link
		display: block
		height: 62px
		min-width: 190px 
		padding: 0 10px 
		border-radius: 4px
		background: #ebebeb
		transition: background 200ms
		text-align: center
		+vertical
		text-decoration: none
		span
			display: inline-block
			vertical-align: middle
			font-size: 14px
			color: #353535
			text-transform: uppercase
			+opensans-sb
		i
			display: inline-block
			vertical-align: middle
			margin-left: 20px
			color: #fff
			&.icon-close-thin
				font-size: 20px
				display: none 	
		&:hover
			background: $green
			span
				color: #fff 
		&:focus, &.active
			+gradient
			span
				color: #fff
				+opensans-eb
		&.active
			i
				&.icon-close-thin
					display: inline-block		
	&.tabs_light
		.tabs__link
			background: #fff
			&:hover
				background: $green
			&:focus, &.active
				+gradient
				span
					color: #fff
					+opensans-eb			 	  
.item-wrap
	+clr
	margin-left: -3px
	.item-col
		float: left
		margin-left: 3px
	+r(1280)
		margin-left: -0.3% 
		.item-col
			width: 24.7%
			margin-left: 0.3%
	+r(1023)
		.item-col
			width: 33%
	+r(768)
		.item-col
			width: 49.7%
	+r(500)
		margin-left: 0 
		.item-col
			width: 100%
			margin-left: 0
			  						 	
.item
	position: relative
	width: 296px
	overflow: hidden
	margin-top: 3px
	background: #ebebeb
	border-radius: 4px
	+r(1280)
		width: 100% 
	.item__img
		height: 203px
		overflow: hidden
		background: url(../img/placeholder.png) center no-repeat
		img
			display: block
			width: 100%
			min-height: 100%
	.item__content
		position: relative
		height: 200px
		padding: 10px 10px 45px 10px
		+r(500)
			height: auto
			min-height: 200px
	.item__title
		font-size: 16px
		color: #323232
		text-decoration: none
		strong
			+opensans-sb
	.item__bottom
		position: absolute
		bottom: 10px
		left: 10px
		right: 10px
	&.item_large
		width: 595px 
		.item__img
			height: 400px
			width: 296px
			float: left
		.item__content
			height: 400px
			width: 299px 
			float: left	 	
.item-more
	padding-top: 20px
	text-align: center 		
.tags
	font-size: 0
	span
		display: inline-block
		vertical-align: middle
		margin-right: 18px
		margin-bottom: 5px
		font-size: 14px
		color: #787878 		
	ul
		display: inline-block
		vertical-align: middle
		li
			display: inline-block
			vertical-align: middle
			margin-left: 8px
			&:first-child
				margin-left: 0 
			a
				display: block
				border-radius: 8px
				background: #fff
				padding: 5px 15px
				font-size: 14px
				color: #787878
				+opensans-sb
				text-decoration: none
				transition: background 200ms, color 200ms
				&:hover
					background: $green
					color: #fff
	&.tags_gray
		ul
			li
				a
					background: #ebebeb
					&.active
						background: #cccbcb 
.date
	font-size: 14px
	color: #929292 				 		   	 
.events
	background: #e6edf6
	padding: 50px 0 
	.events__in
		position: relative
		max-width: 1500px
		padding: 0 10px
		margin: 0 auto
	+r(768)
		padding: 0 17px 35px
		.events__in
			padding: 0 
			.title
				padding: 10px 0
				text-align: center 	  
.chronology-wrap
	width: 100% 
	overflow-x: auto		
.chronology
	position: relative
	min-width: 1440px
	padding: 170px 0 50px
	+clr
	&:before
		content: ""
		position: absolute
		top: 220px
		left: 0
		right: 0
		height: 5px
		background-image: linear-gradient(to right, #e6edf6 0%, #3cafe2 5%, #2e3192 100%)  
		z-index: 1
	+r(768)
		min-width: 1px
		padding: 0 
		&:before
			display: none
	.chronology__nav
		display: none
		position: relative
		padding: 4px 0 
		border-bottom: 1px solid #f1f5fa
		border-top: 1px solid #acadaf
		text-align: center
		font-size: 28px
		+opensans-sb
		color: #6c6c6c
		cursor: pointer
		transition: background 200ms, border-color 200ms
		&:before
			content: "+"
			position: absolute
			top: 5px
			right: 10px
			font-size: 30px
			line-height: 1 
			color: #6c6c6c
		&.is-open
			background: #f1f5fa
			border-top-color: #f1f5fa
			border-bottom-color: #acadaf
			&:before
				content: "-"
		+r(768)
			display: block
	.chronology__content
		+clr
		+r(768)
			display: none
			padding: 20px 0 35px
	.chronology__year
		position: relative
		float: left
		margin-left: 5.5%
		z-index: 2
		+r(768)
			float: none
			width: 100%
			margin-left: 0
		.main-event
			text-align: center
			+r(768)
				float: left
				width: 50%
			+r(480)
				width: 100% 	
			.main-event__title	
				width: 150px
				height: 150px
				padding: 0 20px 
				border-radius: 50%
				margin-bottom: 10px
				+vertical
				span
					display: inline-block
					vertical-align: middle
					font-size: 16px
					color: #fff
				+r(768)
					margin: 0 auto 10px 	 
			.main-event__year
				font-size: 30px
				color: #6c6c6c
				+r(768)
					display: none
		.event
			.clr
				position: relative
				margin-top: 20px
				&:first-child
					margin-top: 0 
				.event__line
					position: absolute
					top: 75px
					left: 38px
